import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { UtilityService } from '../services/utility.service';

@Injectable({
  providedIn: 'root',
})
export class RestService {
  baseURL: string;
  chatURL: string;
  httpHeader = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
  };
  constructor(public http: HttpClient, public utilityService: UtilityService) {
    this.baseURL = this.utilityService.config.adminBaseUrl + '/api/';
  }

  getData(url): Observable<any> {
    const furl = this.baseURL + url;
    return this.http.get(furl);
  }

  postData(url, postParams): Observable<any> {
    const furl = this.baseURL + url;
    return this.http.post(furl, postParams, this.httpHeader);
  }

  // chat with socket
  getChat(url) {
    return this.http.get(this.utilityService.config.chatServerUrl+'/'+url);
  }
  postChat(url, postParams) {
    return this.http.post(
      this.utilityService.config.chatServerUrl + '/' + url,
      postParams,
      this.httpHeader
    );
  }
}
